@import url(https://fonts.googleapis.com/css2?family=Manrope:wght@200;300;400;500;600;700;800&display=swap);
.fl__features-container__feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-direction: row;

    margin: 1rem;
}

.fl__features-container__feature-title {
    flex: 1 1;
    max-width: 180px;
    margin-right: 2rem;
}

.fl__features-container__feature-title h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: #fff;
}

.fl__features-container__feature-title div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    margin-bottom: 0.25rem;
}

.fl__features-container_feature-text {
    flex: 2 1;
    max-width: 390px;
    display: flex;
}

.fl__features-container_feature-text p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 16px;
    line-height: 24px;
    color: var(--color-very-light-blue);
}

@media screen and (max-width: 550px) {
    .fl__features-container__feature-title h1 {
        font-size: 14px;
        line-height: 22px;
    }

    .fl__features-container_feature-text p {
        font-size: 12px;
        line-height: 20px;
    }

    .fl__features-container__feature {
        margin: 1rem 0;
    }
}
.fl__features {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.fl__features-heading {
    flex: 1 1;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-direction: column;

    text-align: left;
    margin-right: 5rem;
}

.fl__features-heading h1 {
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    font-family: var(--font-family);
}

.fl__features-heading p {
    color: var(--color-white);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    font-family: var(--font-family);
    margin-top: 2rem;
}

.fl__features-container {
    flex: 1.5 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
}

@media screen and (max-width: 990px) {
    .fl__features {
        flex-direction: column;
    }

    .fl__features-heading {
        margin: 0 0 2rem 0;
    }
}


@media screen and (max-width: 550px) {
    .fl__features-heading h1 {
        font-size: 28px;
        line-height: 38px;
    }
}
.fl__footer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background: var(--color-blue);
}

.fl__footer-heading {
    width: 100%;
    text-align: center;

    margin-bottom: 2rem;
}

.fl__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 60px;
    line-height: 70px;
}

.fl__footer-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 1rem;
    border: 1px solid #fff;
    text-align: center;
    margin-bottom: 1rem;
    cursor: pointer;
    border-radius: 30px;
}

.fl__footer-btn p {
    font-size: 18px;
    font-family: var(--font-family);
    line-height: 18px;
    color: var(--color-white);
    word-spacing: 5px;
}

.fl__footer-links {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    text-align: left;
}

.fl__footer-links div {
    width: 250px;
    margin: 1rem;
}

.fl__footer-links_div {
    display: flex;
    justify-content: flex-end;
    flex-direction: column;
}

.fl__footer-links_div h4 {
    font-size: 20px;
    line-height: 17px;
    font-family: var(--font-family);
    color: #fff;

    margin-bottom: 0.9rem;
}

.fl__footer-links_div p {
    font-size: 14px;
    line-height: 15px;
    font-family: var(--font-family);
    color: #fff;

    margin: 0.5rem 0;
    cursor: pointer;
}

.fl__footer-copyright {
    margin-top: 2rem;
    text-align: center;
    width: 100%;
}

.fl__footer-copyright p {
    font-size: 12px;
    font-family: var(--font-family);
    line-height: 15px;
    color: #fff;
}

@media screen and (max-width: 850px) {
    .fl__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .fl__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .fl__footer-links div {
        margin: 1rem 0;
    }

    .fl__footer-btn p {
        font-size: 14px;
        line-height: 20px;
    }
}

@media screen and (max-width: 400px) {
    .fl__footer-heading h1 {
        font-size: 27px;
        line-height: 38px;
    }
}
.filelock__header {
    display: flex;
    flex-direction: row;
}

.filelock__header-content {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-direction: column;
    margin-right: 5rem;
}

.filelock__header-content h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 62px;
    line-height: 75px;
    letter-spacing: -0.04em;
}

.filelock__header-content p {
    font-family: var(--font-family);
    font-weight: 400;
    font-size: 20px;
    line-height: 28px;
    color: var(--color-white);

    margin-top: 1.5rem;
}

.filelock__header-content__button button {
    flex: 0.6 1;
    width: 100%;
    min-height: 50px;
    font-family: var(--font-family);
    font-weight: 300;
    font-size: 16px;
    background: var(--gradient-text);
    border: 2px solid var(--gradient-text);
    padding: 0 1rem;
    color: #fff;
    cursor: pointer;
    outline: none;
    margin: 1rem 0 0 1rem;
    border-radius: 25px;
}

.filelock__header-content__button button:hover {
    background: var(--color-white);
    border: 2px solid var(--gradient-text);
    color: var(--gradient-text);
}

.filelock__header-image {
    flex: 1 1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.filelock__header-image img {
    width: 368px;
    height: 240px;
    object-fit: cover;
}

@media screen and (max-width: 1050px) {
    .filelock__header {
        flex-direction: column;
    }

    .filelock__header-content {
        margin: 0 0 3rem;
    }
}

@media screen and (max-width: 650px) {
    .filelock__header-content h1 {
        font-size: 48px;
        line-height: 60px;
    }

    .filelock__header-content p {
        font-size: 16px;
        line-height: 24px;
    }

    .filelock__header-content__button button {
        font-size: 16px;
        line-height: 24px;
    }
}

@media screen and (max-width: 490px) {
    .filelock__header-content h1 {
        font-size: 36px;
        line-height: 48px;
    }

    .filelock__header-content p {
        font-size: 14px;
        line-height: 24px;
    }

    .filelock__header-content__button button {
        font-size: 12px;
        line-height: 16px;
    }
}
